* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    
  }

html {
    overflow-y: scroll; overflow-x:hidden;
    height: 100vh;
}
header{
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}   

body {
    background: #272727;
    font-family: "Montserrat", sans-serif;
    color: #f6f4e6;
    min-height: 100vh;
}

.route{
    display: inline;
    font-weight: 500;
    font-size: 1.2em;
}

.nav_container{
    display: flex;
    width: 700px;
    justify-content: space-between;
    justify-items: center;
    flex-wrap: wrap;
}

.navigation_item{
    color: #f6f4e6;
    text-decoration: none;
    font-size: 1.2em;
    text-transform: uppercase;
    font-weight: 500;
    display: inline-block;
    width: 130px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    text-align: center;
    margin-top: 5px;
}

.navigation_item:hover {
    color: #fddb3a;
}

.active{
    color: #fddb3a;
}

footer{
    flex-shrink: 0;
    margin: 0 auto;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

#root{
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 100vh;
}

main{
    flex: 1 0 auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: column;
}

.footer{
    align-content: center;
}
.search_Container{
    height: 50px;
    width:575px;
    border-radius:30px;
    border:1px solid #dcdcdc;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search_Bar{
    height: 49px;
    border:none;
    width:530px;
    font-size:16px;
    outline: none;
    font-size: 1.5rem;
    font-weight: 200;
    font-family: "Montserrat", sans-serif;
}


.search_Container:hover{
    box-shadow: 1px 1px 8px 1px #dcdcdc;
}

.card-container{
    width: 350px;
    height: 500px;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.315);
    display: flex;
    flex-direction: column;
}

.card-image-container{
    height: 300px;
    width: 350px;
}

.card-image{
    margin: auto;
    display: block;
    width: 100%;
    height: 100%;
}

.card-information-container{
    background-color: white;
    color: #272727;
    text-align: center;
    flex-grow: 1;
    font-family: "Montserrat", sans-serif;
}

.card-person-Name{
    margin-top: 10px;
    font-size: 30px;
    font-weight: 500;
}
.card-sex{
    display: inline-block;
    font-size: 15px;
    font-style: italic;
}

.card-owner-job{
    margin: 10px;
    text-shadow: #272727;
    color: gray;
    font-size: 25px;
    text-decoration: underline;
}

.card-description{
    margin: 10px;
}

.about-flip-card{
    background-color: transparent;
    width:300px;
    height:300px;
    perspective: 1000px;
}

.about-flip-card-inner{
    position: relative;
    width:300px;
    height:300px;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.about-flip-card-front{
    background-color: #bbb;
    color: black;
}

.about-flip-card-back{
    background-image: linear-gradient(#e66465, #9198e5);
    color: white;
    transform: rotateY(180deg);
}

.about-image{
    width:300px;
    height:300px;
    cursor: pointer
}

.about-flip-card-front, .about-flip-card-back {
    position: absolute;
    width:300px;
    height:300px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    cursor: pointer
}

.about-flip-card:hover .about-flip-card-inner {
    transform: rotateY(180deg);
}

.about-flip-card-back h1{
    margin-top: 20px;
}

.about-flip-card-back p{
    margin-top: 30px;
}

.card-gallery{
    gap: 20px;
    margin: 2rem auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 100%;
}

.form-container{
    width: 35rem;
    height: 28rem;
    background-color: #15172b;
    border-radius: 20px;
    display:flex;
    align-items: center;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.25);
    font-size: 1.4rem;
    font-weight: 400;
    font-family: "Montserrat", sans-serif;
}

.form{
    margin: 0px auto;
}


.input-container{margin:10px auto;
display: flex;
align-items: center;}

.input-container .newUserForm-input{
    width: 300px;
}

.input-container .input-label--noncheckbox{
    margin-left: 1rem;
}

.input-container .input-label--checkbox{
    margin-left: 1rem;
}

.newUserForm-input, .newUserForm-input::after{
    height: 2rem;
    cursor: pointer;
}

.newUserForm-input--file{
    display: none;
}

.input-label--file{
    text-decoration: underline;
    cursor:alias
}

.input-label--file:hover{
    transform: scale(1.1);
    color: #fddb3a;
}


.submit-button{
    height: 3rem;
    width: 9rem;
    border-radius: 200px;
    font-size: 1.5rem;
    background-color: #fddb3a;
    cursor: pointer;
}

.Message{
    color: red;
    font-size: 1rem;
    position: absolute;
}

.Message--success{
    color: green;
}

.submit-button:hover{
    transform: scale(1.1);
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.25);
}

.submit-button--clicable{
    background-color: #fddb3a;
}

.submit-button--unclicable{
    background-color: #a2c2af;
}
.toggle {
    cursor: pointer;
    display: inline-block;
  }
  
  .toggle-switch {
    display: inline-block;
    background: #ccc;
    border-radius: 16px;
    width: 58px;
    height: 32px;
    position: relative;
    vertical-align: middle;
    transition: background 0.25s;
  }
  .toggle-switch:before, .toggle-switch:after {
    content: "";
  }
  .toggle-switch:before {
    display: block;
    background: linear-gradient(to bottom, #fff 0%, #eee 100%);
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.25);
    width: 24px;
    height: 24px;
    position: absolute;
    top: 4px;
    left: 4px;
    transition: left 0.25s;
  }
  .toggle:hover .toggle-switch:before {
    background: linear-gradient(to bottom, #fff 0%, #fff 100%);
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
  }
  .toggle-checkbox:checked + .toggle-switch {
    background: #56c080;
  }
  .toggle-checkbox:checked + .toggle-switch:before {
    left: 30px;
  }
  
  .toggle-checkbox {
    position: absolute;
    visibility: hidden;
  }
  
  .toggle-label {
    margin-left: 5px;
    position: relative;
    top: 2px;
  }